import {Event2} from "../../../events/Event2";
import {
  GoodsModel,
  goodsModel
} from "./GoodsModel";

import {
  ii
} from "../requests/pdd/recommendget"
// import {
//   jingfengRequest
// } from "../requests/jd/jingFenQuery";
import {
  vipDdxRecommend,
  vipDdxRecommendNextPage
} from "../requests/vip/vipddxRecommend";
import { fetchTbOptimusMaterial } from "../requests/tb/optimusMaterial";
import {TrackingModel, trackingModel} from "./TrackingModel";
const {getPddRecommendSign, recommendRequestTypes} = ii;
class GoodsController {
  constructor() {

  }
  init = () => {
    let that = this;
    goodsModel.init();
    goodsModel.addEventListener(GoodsModel.events.fetchReadGoodsSignList, function () {
      //  fetchReadGoodsSignList();
      goodsModel.offset = 0;
    });
    trackingModel.addEventListener(TrackingModel.events.eventTrackingUuidChanged, (e) => {
      // console.log('xxxxxxxxxxxxxxxxxxx')
      this.startFetchGoods();
      goodsModel.dispatchEvent(new Event2(GoodsModel.events.fetchReadGoodsSignList));
    });
    goodsModel.addEventListener(GoodsModel.events.needMoreGoodsList, (e) => {
      this.fetchPddGoods();
    });

    goodsModel.addEventListener(GoodsModel.events.pddGoodsListRefreshed, (e) => {
      if (e.data.newList.length > 0) {
        return;
      }
      this.fetchPddGoods();
    });
  }
  startFetchGoods =()=>{
    // if(jingfengRequest.isConnecting)return;
    // fetchTbOptimusMaterial();
    // setTimeout(() => {
    //   getPddRecommendSign(recommendRequestTypes.forRecommend);
    //   jingfengRequest.fetch()
    //   vipDdxRecommend();
    // }, 1000 * 1.5);
    getPddRecommendSign(recommendRequestTypes.forRecommend);
    // jingfengRequest.fetch()
    vipDdxRecommend();
  }
  // startFetchGoods =()=>{
  //   if(jingfengRequest.isConnecting)return;
  //   // fetchTbOptimusMaterial();
  //   // setTimeout(() => {
  //   //   getPddRecommendSign(recommendRequestTypes.forRecommend);
  //   //   jingfengRequest.fetch()
  //   //   vipDdxRecommend();
  //   // }, 1000 * 1.5);
  //   getPddRecommendSign(recommendRequestTypes.forRecommend);
  //   jingfengRequest.fetch()
  //   vipDdxRecommend();
  // }
  // fetchTbGoods = ()=>{
  //   console.log('from first')
  // }
  fetchPddGoods = () => {
    if (goodsModel.isFetchingGoods) {
      return;
    }
    if (goodsModel.recommendsTotallyFetched) {
      return;
    }
    goodsModel.isFetchingGoods = true;
    getPddRecommendSign(recommendRequestTypes.forRecommend);
    // jingfengRequest.fetchNextPage();
    vipDdxRecommendNextPage();
    fetchTbOptimusMaterial();
  }

}

const goodsController = new GoodsController();
export {
  goodsController,
  GoodsController
}