import {f7} from "framework7-react";

const { goodsModel } = require("../../mc/GoodsModel");
const { endpoints } = require("../../mc/pddRequests/PddEndpoints");
const { TmPddRequest } = require("../../remotes/TmPddRequest");
const {TMs} = require("../../remotes/TMs");
const TAG = "pdd goods search";
function onMemberResult(res){
  // console.log(TAG,"onMemberResult is", res);
  if(!res){
    return f7.toast.create({closeTimeout: 3000,text:TAG + "没有返回数据", destroyOnClose:true}).open();
    // return wx.showToast({
    //   title: TAG + "没有返回数据",
    // });
  }
  if(!res.is_ok){
    return f7.toast.create({closeTimeout: 3000,text:`${TAG} ${res.msg}`, destroyOnClose:true}).open();
    // return wx.showToast({
    //   title: TAG + res.msg,
    // });
  }
  // if(!res.params){
  //   return f7.toast.create({closeTimeout: 3000,text:`${TAG}没有返回签名数据`, destroyOnClose:true}).open();
  // }
  let data = JSON.parse(res.res_text);
  onThirdResult(data);
  // request.fetchPdd(res.params);
}
function onMemberError(res){
  console.log(TAG,"onMemberError is", res);
}
function onMemberComplete(res){
  // console.log(TAG,"onMemberComplete is", res);
}

function onThirdResult(res){
  // console.log(TAG,"onThirdResult is ", res);
  if(res && res.error_response){
    return f7.toast.create({closeTimeout: 3000,text:`pdd搜索时返回错误信息`, destroyOnClose:true}).open();
  }
  if(!res || !res.goods_search_response || !res.goods_search_response.goods_list){
    return f7.toast.create({closeTimeout: 3000,text:`pdd没有返回数据`, destroyOnClose:true}).open();
    // return wx.showToast({
    //   title: 'pdd没有返回数据',
    // });
  }
  if(res && res.goods_search_response && 
    res.goods_search_response.goods_list && 
    res.goods_search_response.goods_list.length == 1 && 
    !(res.goods_search_response.goods_list[0].goods_name)){
      goodsModel.setPddSearchResult({goods_search_response:{goods_list:[]}})
      // request.fetch({goods_sign_list:[res.goods_search_response.goods_list[0].goods_sign], use_customized:false, with_coupon:false});

      
      // getPromotionUrlSign([goods.goods_sign], goods.search_id, goods, true);
      // getPromotionUrlSign([res.goods_search_response.goods_list[0].goods_sign], "", null, )
      return;
  }
  goodsModel.setPddSearchResult(res.goods_search_response);

}

function onThirdError(res){
  console.log(TAG,"onThirdError is", res);

}

function onThirdComplete(res){
  // console.log(TAG,"onThirdComplete is", res);

}

class SearchRequest extends TmPddRequest{
  constructor( onTmResult, onTmError, onTmComplete, endpoint, onPddResult, onPddError, onPddComplete){

    super(TMs.TPddGoods, TMs.MPddGoodsSearch, onTmResult, onTmError, onTmComplete, endpoint, onPddResult, onPddError, onPddComplete);
  }
  // fetch = (params)=>{
  //   // super.fetch(params);
  //   console.log('params is', params);
  //   this.tmParams = params;
  //   this.tm.run();
  // }
}

export const pddGoodsSearchRequest = new SearchRequest(onMemberResult, onMemberError, onMemberComplete, endpoints.goodsSearch(),
onThirdResult, onThirdError, onThirdComplete);
const request = pddGoodsSearchRequest;
