const MainModel = require("../mc/MainModel");
const Member = require("./Member");
const { TMRequest, EndPointRequest } = require("./Request");
const TMs = require("./TMs");
export class TmPddRequest{
  constructor(team, member, onTmResult, onTmError, onTmComplete, endpoint, onPddResult, onPddError, onPddComplete){
    this.tm = new TMRequest(Member.gateway, team, member, onTmResult, onTmError, onTmComplete, this.getTmData);
    this.pdd = new EndPointRequest(endpoint, onPddResult, onPddError, onPddComplete, this.getPddData);
    this.tmParams = null;
    this.pddParams = null;
    this.isConnecting = false;
  }
  fetch = (params) =>{
    this.tmParams = params;
    this.tm.run();
  }
  fetchPdd = (params)=>{
    this.pddParams = params;
    this.pdd.run();
  }
  getTmData = ()=>{
    let params;
    if("params" in this.tmParams){
      params = this.tmParams;
    }else{
      params = {
        from:"web",
        open_key_uuid:MainModel.openid,
        params:this.tmParams,
      };
    }
    return params;
  }
  getPddData = ()=>{
    return this.pddParams;
  }
}

