
import React from "react";
import {userModel} from "../../mc/UserModel";
import {User} from "../../vo/user/User";
import {LoginPageContent} from "./LoginPage";
import {LoggedPage} from "./LoggedPage";
import {ComponentBase} from "../components/ComponentBase";
import {Page} from "framework7-react";
class MinePageContent extends ComponentBase{
    constructor(props) {
        super(props);
        this.state = {user:userModel.user};
        this._addEvents();
    }
    _addEvents = ()=>{
        userModel.user.addEventListener(User.eventIsLoggedChanged, this._onUserLoggedChanged);
    }
    _onUserLoggedChanged = (e)=>{
        this.setState({user:userModel.user});
    }
    render = ()=>{
        if(!this.state.user.isLogged){
            return <LoginPageContent />;
        }
        return <LoggedPage/>
    }
}

function MinePage (){
    return <Page pageContent={false}>
        <MinePageContent />
    </Page>
}

export {MinePageContent, MinePage}