import React from "react";
import {Button, f7} from "framework7-react";
import {isMobile} from "../../../utils/utils";
import {Dom7} from "framework7";
import Member from "../../../remotes/Member";
import tms from "../../../remotes/TMs";
import {MainModel, mainModel} from "../../../mc/MainModel";
import {Event2} from "../../../../../events/Event2"
import {UserModel, userModel} from "../../../mc/UserModel";
import {isDev} from "../../../confs/env";

export default class RegMobile extends React.Component{
    constructor(props) {
        super(props);
        this.codeMember = null;
        this.loginMember = null;
        this.state = {mobile:isDev ? "1312112956" : "", isLegalMobile:false, isFetchingCode:false, code:""};
        this._addEvents();
    }
    _addEvents = ()=>{

    }
    _onMobileChange = (e)=>{
        let v = e.target.value;
        this["state"].mobile = v;
        this.setState({mobile: v, isLegalMobile:false})
        if(!v)return;
        if(v.length < 11)return;
        if(!isMobile(v)){
            f7.dialog.alert('不正确的手机号，请重新填写。', ()=>{
                Dom7('#mobile').focus();
            });
            return;
        }
        this.setState({isLegalMobile:true});
    }
    _onCodeChange = (e)=>{
        let v = e.target.value;
        this.setState({code:v});
    }
    _onFetchCodeClick = ()=>{
        if(!this.state.isLegalMobile)return;
        this._fetchCode();
    }
    _onLoginClick = ()=>{
        if(this.loginMember)return;
        let member = this.loginMember = new Member(tms.TVisitor, tms.MVisitorLoginByCode, {code:this.state.code, account_type:"web", mobile:this.state.mobile}, (res)=>{
            // console.log(`tms.TVisitor, tms.MVisitorLoginByCode res is`, res)
            userModel.dispatchEvent(new Event2(UserModel.events.fetchLogin));
        }, (member)=>{
            console.log(`tms.TVisitor, tms.MVisitorLoginByCode net error is`, member.netError, member.netErrorInfo);
        }, (member)=>{
            console.log(`tms.TVisitor, tms.MVisitorLoginByCode data error is`, member.dataError, member.dataErrorInfo);
        }, (member) =>{
            this.loginMember = null;
            // console.log(`tms.TVisitor, tms.MVisitorLoginByCode complete is`, member.isNetOk, member.isDataOk, member.isTotallyOk);
        }, null, true, true);
        // member.send();
        mainModel.dispatchEvent(new Event2(MainModel.events.addWaitingMember, member.send));
    }
    _fetchCode(){
        if(!this.state.isLegalMobile){
            f7.dialog.alert('不正确的手机号，请重新填写。', ()=>{
                Dom7('#mobile').focus();
            });
            return;
        }
        if(this.codeMember)return;
        let member = this.codeMember = new Member(tms.TVisitor, tms.MVisitorVerificationCode, {mobile:this.state.mobile}, (res)=>{
            console.log(`tms.TVisitor, tms.MVisitorVerificationCode res is`, res)
        }, (member)=>{
            console.log(`tms.TVisitor, tms.MVisitorVerificationCode net error is`, member.netError, member.netErrorInfo);
        }, (member)=>{
            console.log(`tms.TVisitor, tms.MVisitorVerificationCode data error is`, member.dataError, member.dataErrorInfo);
        }, (member) =>{
            // console.log(`tms.TVisitor, tms.MVisitorVerificationCode complete is`, member.isNetOk, member.isDataOk, member.isTotallyOk);
            this.codeMember = null;
        }, null, true, true);
        // this.codeMember.send();
        mainModel.dispatchEvent(new Event2(MainModel.events.addWaitingMember, member.send));
        this.setState({isFetchingCode:true});
    }
    render = () =>{
        return <div><form className="list" id="my-form">
            <ul>
                <li>
                    <div className="item-content item-input">
                        <div className="item-inner">
                            <div className="item-title item-label">手机号码</div>
                            <div className="item-input-wrap">
                                <input id={"mobile"} maxLength={11} onChange={this._onMobileChange}
                                       value={this.state.mobile}
                                       type="number" name="mobile" placeholder="请输入您的手机号码"/>
                            </div>
                        </div>
                    </div>
                </li>
                <li>
                    <div className="item-content item-input">
                        <div className="item-inner col">
                            <div className="item-title item-label ">验证码</div>
                            <div className="item-input-wrap ">
                                <input onChange={this._onCodeChange} maxLength={6} type="number" name="code" placeholder="请输入手机接收到的验证码"/>
                            </div>
                        </div>
                        <div className={"item-media"}>
                            <Button className={this.state.isLegalMobile ? "" :'color-gray'} onClick={this._onFetchCodeClick} >获取</Button>
                        </div>
                    </div>
                </li>
                <li>
                    <div className="item-content item-input">
                        <div className={"item-media"}>
                            <Button className={this.state.isLegalMobile ? "" :'color-gray'} onClick={this._onLoginClick} >登录</Button>
                        </div>
                    </div>
                </li>
            </ul>
        </form></div>
    }
}