
export const saveKeys = {
  pdd:"pddRecommendPageInfo",
  "jd":"jdRecommendPageInfo",
  "vip":"vipRecommendPageInfo",
  "tb":"tbRecommendPageInfo",
}
export class PageInfo {
  constructor(){
    this.total = -1;
  }
  reset = (index, limit, channel)=>{
    this.index = index;
    this.limit = limit;
    this.channel = channel;
  }
}
export class RecommendPageInfo {
  constructor(){
    this.pageIndex = -1;
    this.pageLimit = 20;
    this.channels = [];
    this.map =  {};
    this._channels = [];
    this.channelIndex = -1;
    this.name = "";
  }
  init = (name, channels, pageStartIndex, limit)=>{
    this.name = name;
    this.channels = channels;
    this.pageIndex = pageStartIndex;
    this.pageLimit = limit;
  }
  _reset = ()=>{
    for(let i = 0; i < this.channels.length; i ++){
      let c = this.channels[i];
      let pageInfo = this.map[c] || new PageInfo();
      pageInfo.reset(this.pageIndex, this.pageLimit, c);
      this.map[c] = pageInfo;
    }
    this._channels = this.channels.slice();
    this.channelIndex = -1;
  }
  getNextPageInfo = ()=>{
    if(this._channels.length == 0){
      this._reset();
    }
    let index = Math.min(this.channelIndex + 1, this._channels.length - 1);
    let channel = this._channels[index];
    let pageInfo = this.map[channel];
    if(pageInfo.index > -1 && pageInfo.total > -1){
      let off = pageInfo.index * pageInfo.limit;
      if(off >= pageInfo.total){
        this._channels.splice(index, 1);
        return this.getNextPageInfo();
      }
    }
    pageInfo.index ++;
    if(index == this._channels.length - 1){
      index = -1;
    }
    this.channelIndex = index;
    return pageInfo;

  }
  save = ()=>{
    console.log('on recommend page save ', this);
  }
}

