export function fetchTbOptimusMaterial(){
  pageInfo = recommendPageInfo.getNextPageInfo();
  let params = {material_id:pageInfo.channel, page_size:pageInfo.limit, page_no:pageInfo.index};
  console.log('params is', params);
  member.body = {params}
  member.send();
}

