
import React from "react";
import {Button, Page, PageContent} from "framework7-react";
export class IndexPageContent extends React.Component{
    _copy = async ()=>{
        if(!navigator.clipboard){
            return;
        }

        let url = "http://localhost:3012/icons/168.png";
        const response = await fetch(url);
        const blob = await response.blob();

        var type = "text/plain";
        var blob2 = new Blob(["hehehe"], { type });
        var data2 = new window.ClipboardItem({ [type]: blob });
        const item = new window.ClipboardItem({'image/png': blob});
        await navigator.clipboard.write([item]).then(function(e) {
            console.log("Copied to clipboard successfully!", e, item);
        }, function(error) {
            console.error("unable to write to clipboard. Error:");
            console.log(error);
        });

        return;
        let data = new DataTransfer();

        data.items.add("text/plain", "ooxx");
        navigator.clipboard.write(data).then(function() {
            /* success */
            console.log("ooxx successed")
        }, function(e) {
            /* failure */
            console.log("ooxx failure", e)
        });
    }
    render() {
        return (<PageContent  style={{paddingTop:"0px"}}>
            {/*<div>您的账号没有包裹，快去寻找心仪的商品吧！！！   </div>*/}
            <div className={"beian"}>京ICP备13050035号-11</div>
            <Button onClick={this._copy}>copy</Button>
        </PageContent>)

    }
}
export function ExpressPackages(){
    return <Page pageContent={false}>
        <IndexPageContent />
    </Page>
}

