function checkSearchUrlInfo(str){
  let res = {isUrl:false, platform:"", tbUrl:""};
  if(checkIsVipUrl(str)){
    res.isUrl = true;
    res.platform = "vip";
    return res;
  }
  if(checkIsPddUrl(str)){
    res.isUrl = true;
    res.platform = "pdd";
    return res;
  }
  if(checkIsJdUrl(str)){
    res.isUrl = true;
    res.platform = "jd";
    return res;
  }
  let {isUrl, url} = checkIsTbUrl(str)
  if(isUrl){
    res.isUrl = true;
    res.tbUrl = url;
    res.platform = "tb";
    return res;
  }
  // if(str.indexOf("vip.com/") > -1){
  //   res.isUrl = true;
  //   res.platform = "vip";
  //   return res;
  // }
  // if(str.indexOf("yangkeduo.com") > -1){
  //   res.isUrl = true;
  //   res.platform = "pdd";
  //   return res;
  // }
  // if(str.indexOf("jd.com") > -1){
  //   res.isUrl = true;
  //   res.platform = "jd";
  //   return res;
  // }
  return res;
}

