function checkIsTbUrl(str){
  let list = ["tb.cn", "tb.com", "taobao.com", "taobao.cn", "tmall.com", "tmall.cn"];
  // list = [];
  let res = {isUrl:false, url:""}
  for(let i = 0; i < list.length; i++){
    if(str.indexOf(list[i]) > -1){
      let matches = str.match(/\bhttps?:\/\/\S+/gi);
      if(matches.length == 1){
        res.isUrl = true;
        res.url = matches[0];
        return res;
      }
    }
  }
  return res;
}

function checkIsPddUrl(str){
  let list = ["yangkeduo.com", "yangkeduo.cn", "pinduoduo.com", "pinduoduo.cn"];
  for(let i = 0; i < list.length; i++){
    if(str.indexOf(list[i]) > -1)return true;
  }
  return false;
}

function checkIsJdUrl(str){
  let list = ["jd.com", "jd.cn"];
  for(let i = 0; i < list.length; i++){
    if(str.indexOf(list[i]) > -1)return true;
  }
  return false;
}

function checkIsVipUrl(str){
  let list = ["vip.com/", "vip.cn/"];
  for(let i = 0; i < list.length; i++){
    if(str.indexOf(list[i]) > -1)return true;
  }
  return false;
}
function completeSearchUrl(str){
  if(str.indexOf("https://") == 0)return str;
  if(str.indexOf("//") == 0)return `https:${str}`;
  let arr = str.split('/');
  let index = str.indexOf('/');
  let head = str.slice(0, index);
  head = head.replace(/\s+/g,"");
  let end = str.slice(index);
  let url = `https://${head}${end}`;
  return url;
}
module.exports = {checkSearchUrlInfo, completeSearchUrl}