export const ii = {};
let _goods;
let _needDetail = false;
let _detailCb;
let _goodsSignList;
(() => {
  function onSuccess(res) {
    console.log('get pdd goods promotionurl sign  res is', res);
    if (!res || !res.params) {
      return;
    }
    let obj;
    if(_needDetail){
      let txt = res.res_detail_text;
      try{
        obj = JSON.parse(txt);
      }catch(e){
        return showSimpleToast('解析拼多多商品详情时发生错误');
      }
      onPddDetailSuccess(obj, _goodsSignList);
      // fetchPddDetail(res.detail, _goodsSignList);
    }
    obj = null;
    let txt = res.res_text;
    try{
      obj = JSON.parse(txt);
    }catch (e){
      return showSimpleToast("解析拼多多商品优惠时发送错误")
    }
    onPddSuccess(obj);
    // fetchPddPromotionUrl(res.params, _goodsSignList);
  }

  function onError(member) {

  }

  function onNetError(member) {


  }

  function onComplete(member) {

  }
  let request; // = new Member(TMs.TPddGoods, TMs.MPddGoodsRecommendGet, {}, onSuccess, onError, onComplete);
  //到宝贝服务获取商品id列表
  function getPromotionUrlSign(goodsSignList, searchId, goods, needDetail, detailCb) {
    _detailCb = detailCb;
    _goods = goods;
    _goodsSignList = goodsSignList;
    console.log('_goodsSignList 1 is', _goodsSignList);
    _needDetail = needDetail;
    let m = {};
    m["generate_mall_collect_coupon"] = true;
    m["generate_we_app"] = true;
    m["generate_short_url"] = true;
    m["generate_schema_url"] = true;
    m["goods_sign_list"] = goodsSignList;
    m["search_id"] = searchId;
    m["type"] = "pdd.ddk.goods.promotion.url.generate";
    request = new Member(TMs.TPddGoods, TMs.MPddPromotionUrlGenerate, {
      "params": m, need_detail:needDetail,from:"web"
    }, onSuccess, onError, onComplete);
    request.send();
  }
  ii.getPromotionUrlSign = getPromotionUrlSign;
})();

let fetchPddPromotionUrl;
let onPddSuccess
let prevSearchId;
(() => {
  onPddSuccess = onSuccess;
  function onSuccess(res) {
    console.log('onPddSuccess1 is', res);
    // console.log('fetch goods promotion url from pdd  res is', res);
    if (!res || !res.goods_promotion_url_generate_response || 
      !res.goods_promotion_url_generate_response.goods_promotion_url_list ||
      res.goods_promotion_url_generate_response.goods_promotion_url_list.length == 0) {
      return f7.toast.create({closeTimeout: 3000,text:`获取跳转拼多多数据失败`, destroyOnClose:true}).open();
      //   wx.showToast({
      //     title: '获取跳转拼多多数据失败',
      //   })
      // return;
    }
    let goods = res.goods_promotion_url_generate_response.goods_promotion_url_list[0];
    goods.goods_sign = _goodsSignList[0];
    if(_goods){
      _goods.we_app_info = goods.we_app_info;
      _goods.mobile_short_url = goods.mobile_short_url;
      _goods.mobile_url = goods.mobile_url;
      _goods.schema_url = goods.schema_url;
      _goods.short_url = goods.short_url;
      _goods.url = goods.url;
      goodsModel.dispatchEvent(new Event2(GoodsModel.events.onPddWeAppInfoLoaded, _goods));
      return;
    }
    goodsModel.setPddWeAppInfo(goods);
    console.log('onPddSuccess2 is', goods);
  }

  function onError(member) {

  }

  function onNetError(member) {


  }

  function onComplete(member) {

  }
  let request;
  // let _goodsSignList;
  function pdd(params, goodsSignList) {
    _goodsSignList = goodsSignList;
    request = new PddMember(endpoints.goodsRecommendGet(), params, onSuccess, onError, onComplete);
    request.send();
  }
  fetchPddPromotionUrl = pdd
})();

let fetchPddDetail;
let onPddDetailSuccess;
(() => {
  onPddDetailSuccess = onSuccess;
  function onSuccess(res) {
    console.log('onPddDetailSuccess1 is', res);
    // console.log('fetch goods detail from pdd  res is', res);
    if (!res || !res.goods_detail_response || 
      !res.goods_detail_response.goods_details ||
      res.goods_detail_response.goods_details.length == 0) {
      return f7.toast.create({closeTimeout: 3000,text:`获取跳转拼多多数据失败`, destroyOnClose:true}).open();
      //   wx.showToast({
      //     title: '获取跳转拼多多数据失败',
      //   })
      // return;
    }
    let goods = res.goods_detail_response.goods_details[0];
    goods.goods_sign = _goodsSignList[0];
    if(_goods){
      _.assign(_goods, goods);
      goodsModel.parsePddGoods(_goods);
      goodsModel.dispatchEvent(new Event2(GoodsModel.events.onPddWeAppInfoLoaded, _goods));
      return;
    }
    goodsModel.setPddWeAppInfo(goods);
    console.log('onPddDetailSuccess 2 is', goods);
  }

  function onError(member) {

  }

  function onNetError(member) {


  }

  function onComplete(member) {

  }
  let request;
  // let _goodsSignList;
  function pdd(params, goodsSignList) {
    _goodsSignList = goodsSignList;
    console.log('_goodsSignList 3 is', _goodsSignList);
    request = new PddMember(endpoints.goodsGetGoodsDetail(), params, onSuccess, onError, onComplete);
    request.send();
  }
  fetchPddDetail = pdd
})();
export const getPromotionUrlSign = ii.getPromotionUrlSign;
