const { goodsModel } = require("../../mc/GoodsModel");
const {DdxRequest} = require("../../remotes/DdxMember");
const { RecommendPageInfo, saveKeys, PageInfo } = require("../recommendPageInfo");

let endpoint = "//api.tbk.dingdanxia.com/vip/goodsList";
const TAG = "vip ddx recommend tag";
const recommendPageInfo = new RecommendPageInfo();
recommendPageInfo.init(saveKeys.vip, [1], 0, 10);
let pageInfo = new PageInfo();
function onSuccess(res){
  // console.log(TAG, "vip ddx recommend onSuccess is", res);
  if(!res.data)return;
  goodsModel.setVipGoodsList(res.data);
  // goodsModel.setVipDdxSearchResult(res);
}
function onFail(res){
  console.log(TAG, "onFail is", res);
}
function onComplete(res){
  // console.log(TAG, "onComplete is", res);
}

const req = new DdxRequest(endpoint, null, onSuccess, onFail, onComplete);

export function vipDdxRecommend(){
  if(req.isConnecting)return;
  pageInfo = recommendPageInfo.getNextPageInfo();
  // let params = {page:page, pageSize:limit, channelType:1}
  let params = {page:pageInfo.index, pageSize:pageInfo.limit, channelType:pageInfo.channel}
  req.send(endpoint, params);
}
export function vipDdxRecommendNextPage(){
  page ++;
  vipDdxRecommend();
}
let page = 1;
let limit = 10;
resetPageInfo();
export function resetPageInfo(){
  page = 1;
  limit = 10;
}

