import {Event2 as Event} from "../../../../events/Event2";
import {showSimpleToast} from "../../uiuitils/toast";
import {DdxRequest} from "../../remotes/DdxMember"
import {userModel} from "../../mc/UserModel";
const { goodsModel, GoodsModel } = require("../../mc/GoodsModel");
const _ = require('lodash')
let endpoint = "//api.tbk.dingdanxia.com/jd/by_unionid_promotion";
const TAG = "jd ddx goods detail tag"
function onSuccess(res){
  if(!res || !res.data){
    return showSimpleToast("没有获取到jd链接")
  }
  let we_app_info = {app_id:"wx91d27dbf599dff74", page_path:'pages/union/proxy/proxy?spreadUrl=' + res.data.shortURL}
  _goods.we_app_info = we_app_info;
  _.assign(_goods, res.data);
  goodsModel.parseJdGoods(_goods);
  _that.setJdGoods({goods:_goods})
  goodsModel.dispatchEvent(new Event(GoodsModel.events.onPddWeAppInfoLoaded, _goods));
}
function onFail(res){
  console.log(TAG, "onFail is", res);
  _that.setJdGoodsFailed({debugInfo:JSON.stringify(res)})
  showSimpleToast(`jd ${res}`)
}
function onComplete(res){
  // console.log(TAG, "onComplete is", res);
}

const req = new DdxRequest(endpoint, null, onSuccess, onFail, onComplete);

let _goods;
let _that;
export function jdDdxGetGoodsDetail(goods, that){
  _goods = goods;
  _that = that;
  // let params = {positionId:MainModel.user ? MainModel.user.mobile :1000, "unionId":2016490606, 
  // materialId:goods.materialUrl,autoSearch:true}
  let id = userModel.user.id || "00000000";
  id = fixId("0", 8, id);
  // let pid =
  let params = {positionId: userModel.user.mobile || 1000, "unionId":2016490606,
  materialId:goods.materialUrl,autoSearch:true}
  // MainModel.logs.push("dingdanxia " + JSON.stringify(params))
  if(goods.bestCoupon){
    // params.couponUrl = encodeURIComponent(goods.bestCoupon.link);
  }
  req.send(endpoint, params);
}

function fixId(prefix, len, value){
  while(value.length < len){
    value = prefix + value;
  }
  return value;
}