
import React from "react";
import {f7, Navbar, Page, PageContent} from "framework7-react";
import {goodsSearchRouter} from "../goodssearch/goodsSearchRouter";
import {ComponentBase} from "../components/ComponentBase";
import {goodsDetailPageRouter} from "../goodsdetail/goodsDetailPageRouter";

export class DiscountPageContent extends React.Component{
    constructor(props) {
        super(props);
        this.state = {};
        this._addEvents();
    }
    _addEvents = ()=>{

    }
    _onSearchbarFocusIn = ()=>{
        document.activeElement.blur();
        // console.log('f7.views.main.router is', f7.views.main.router);
        // f7.views.main.router.navigate({"path":"/goodssearch"})
        f7.views.main.router.navigate({"path":goodsSearchRouter.path})
        // f7.views.main.router.navigate({"path":goodsDetailPageRouter.path})

    }
    render = ()=>{
        return (
            <div className="navbar">
                <div className="navbar-bg"></div>
                <div className="navbar-inner sliding">
                    <form className="searchbar" style={{width:"100%"}}>
                        <div className="searchbar-inner">
                            <div className="searchbar-input-wrap">
                                <input onFocus={this._onSearchbarFocusIn} type="search" placeholder="输入商品链接或标题" />
                                <i className="searchbar-icon"></i>
                                <span className="input-clear-button"></span>
                            </div>
                            <span className="searchbar-disable-button">Cancel</span>
                        </div>
                    </form>
                </div>
            </div>)
    }
}

export class DiscountPage extends ComponentBase{
    constructor(props) {
        super(props);
    }
    _loadMore = ()=>{
        console.log('load more')
    }
    render = ()=>{
        let cn = "infinite-scroll-content infinite-scroll-top";
        cn = ""
        return <Page pageContent={false}>
            <DiscountPageContent />
            <PageContent className={cn}
                         // infinite
                         // infiniteDistance={50}
                         // infinitePreloader={showPreloader}
                         // onInfinite={this._loadMore}
            >
                <div>本站主要提供电商平台找返利和找券的功能。</div>
                <div>例如京东、淘宝、拼多多平时购买的商品，大多数是有返利的，通过我们平台您能拿到全网最高返利，因为我们不像其它中间商抽成，我们是通过做大流量来出售流量赚钱的。</div>
                {/*<div>您</div>*/}
                {/*<div className="preloader infinite-scroll-preloader"></div>*/}
            </PageContent>
        </Page>
    }
}