import {f7} from "framework7-react";
import {CrossEvent, CrossMember} from "./CrossMember";

export class JdMember{
  constructor(onSuccess, onError, onComplete){
    this.onSuccess = onSuccess;
    this.onError = onError;
    this.onComplete = onComplete;
    this.retryCount = 0;
    this.member = null;
    this.rawQuery = "";
    this.isConnecting = false;
  }
  send(rawQuery){
    let gateway = JdMember.gateway;
    let header = {
      'content-type': 'application/json',
    };

    let member = this.member = new CrossMember();
    member.addEventListener(CrossEvent.EventTypes.onError, this._onError);
    member.addEventListener(CrossEvent.EventTypes.onSuccess, this._onSuccess);
    member.addEventListener(CrossEvent.EventTypes.onComplete, this._onComplete);
    member.load(CrossMember.Methods.get, gateway + "?" + rawQuery, null, header);
  }

  _onError = (e)=>{
    if(this.onFail){
      this.onFail(e);
    }
  }
  _onSuccess = (e)=>{
    let res = e.res;
    if(!res || !res.data || res.data.code != 200){
      if(this.onFail){
        this.onFail(res);
      }
      return f7.toast.create({closeTimeout: 3000,text:'订单侠京东没有返回数据', destroyOnClose:true}).open();
    }
    if(this.onSuccess){
      this.onSuccess(res.data);
    }
  }
  _onComplete = (e)=>{
    this.isConnecting = false;
    if(this.onComplete){
      this.onComplete();
    }
  }
}
JdMember.gateway = "https://api.jd.com/routerjson";
// export JdMember;