import {MainModel, mainModel} from "../../mc/MainModel";
import {Event2} from "../../../../events/Event2";
import {userModel} from "../../mc/UserModel"
import tms from "../../remotes/TMs";
import Member from "../../remotes/Member";
import {User} from "../../vo/user/User";
import {formatDate} from "../../utils/utils";
export function listenPackWaitingList(){
    let member = new Member(tms.TPack, tms.MPackWaitingList, {}, (res)=>{
        // console.log(`tms.TPack, tms.MPackWaitingList res is`, res)
        parse(res);
    }, (member)=>{
        console.log(`tms.TPack, tms.MPackWaitingList net error is`, member.netError, member.netErrorInfo);
    }, (member)=>{
        console.log(`tms.TPack, tms.MPackWaitingList data error is`, member.dataError, member.dataErrorInfo);
    }, (member) =>{
        // console.log(`tms.TPack, tms.MPackWaitingList complete is`, member.isNetOk, member.isDataOk, member.isTotallyOk);
    }, null, true, true);
    userModel.user.addEventListener(User.eventIsLoggedChanged, ()=>{
        if(!userModel.user.isLogged){
            mainModel.userPacksInfo.clear();
            return;
        }
        mainModel.dispatchEvent(new Event2(MainModel.events.addWaitingMember, member.send));
    })
}

function parse(res){

    // let hasWaitingPacks = false;
    // let hasTakenPacks = false;
    let house_list = res.house_list;
    let takenList = [];
    for (let i = 0; i < house_list.length; i++) {
        let houseInfo = house_list[i];
        houseInfo.houseTakeAllStatus = "default"
        houseInfo.packLength = 0;
        houseInfo.takenPackLength = 0;
        houseInfo.btnName = "领取";
        houseInfo.btnState = "normal";
        houseInfo.qrTakeData = {
            mobiles: []
        }
        houseInfo.hasSelectedPacks = false;
        let mobileList = houseInfo.mobile_list;
        for (let j = 0; j < mobileList.length; j++) {
            let mobileInfo = mobileList[j];
            let qrMobile = {
                mobile: mobileInfo.mobile,
                packs: []
            };
            mobileInfo.hasWaitingPackage = false;
            mobileInfo.hasTaken = false;
            mobileInfo.waitingPackLength = 0;
            mobileInfo.takenPackLength = 0;
            let packList = mobileInfo.pack_list;
            for (let l = 0; l < packList.length; l++) {
                let pack = packList[l];
                parsePicIdToList(pack);
                let date = new Date(pack.create_time);
                pack.create_time = date;
                pack.createTime = formatDate(date,"yyyy-MM-dd hh:mm:ss");
                if (pack.is_taken == false) {//eslint-disable-line
                    houseInfo.hasSelectedPacks = true;
                    pack.checked = true;
                    houseInfo.packLength++;
                    pack.btnState = "normal";
                    pack.btnName = "单独取";
                    mobileInfo.hasWaitingPackage = true;
                    mobileInfo.waitingPackLength++;
                    // hasWaitingPacks = true;
                    qrMobile.packs.push({
                        b: pack.barcode,
                        u: pack.uuid
                    });
                } else {
                    houseInfo.takenPackLength++;
                    mobileInfo.takenPackLength++;
                    // hasTakenPacks = true;
                    mobileInfo.hasTaken = true;
                    pack.takenTime = formatDate(date,"yyyy-MM-dd hh:mm:ss");
                    takenList.unshift(pack);
                }
            }
            if (mobileInfo.waitingPackLength > 0) {
                houseInfo.qrTakeData.mobiles.push(qrMobile);
            }
        }
    }
    // console.log("houseList is ", house_list);
    // MainModel.house_list = res.house_list;
    // util.laterCallSetData(this, {
    //     house_list: MainModel.house_list,
    //     hasWaitingPacks: hasWaitingPacks,
    //     hasTakenPacks: hasTakenPacks,
    //     takenList: takenList
    // });
}


function parsePicIdToList(pack) {
    // console.log('pack is', pack);
    if (!pack) return;
    let images = pack.pic_id.split(',');
    if (!images || images.length === 0) return;
    let list = [];
    for (let i = 0; i < images.length; i++) {
        let jpg = images[i];
        if (!jpg || jpg === ",") {
            continue;
        }
        list.push({
            width: 300,
            height: 800,
            url: "http://pic2.express-src.qiniu.shangwoa.com/" + jpg + "?a=" + Date.now()
        });
    }
    pack.images = list;
}