const { goodsModel } = require("../../mc/GoodsModel");
const {DdxRequest} = require("../../remotes/DdxMember");

let endpoint = "//api.tbk.dingdanxia.com/jd/query_goods";
const TAG = "jd ddx search query tag"
function onSuccess(res){
  // console.log(TAG, "onSuccess is", res);
  goodsModel.setJdDdxSearchResult(res);
}
function onFail(res){
  console.log(TAG, "onFail is", res);
}
function onComplete(res){
  // console.log(TAG, "onComplete is", res);
}

const req = new DdxRequest(endpoint, null, onSuccess, onFail, onComplete, " 京东搜索");

function jdDdxSearchQuery(params){
  if(req.isConnecting)return;
  req.send(endpoint, params, 'jd search');
}

module.exports = {jdDdxSearchQuery}