import {showSimpleToast} from "../../uiuitils/toast";
import {userModel} from "../../mc/UserModel";
import {getECommerceChannelVip} from "../../confs/env";

const {Event2} = require("../../../../events/Event");
const { goodsModel, GoodsModel } = require("../../mc/GoodsModel");
const {DdxRequest} = require("../../remotes/DdxMember");
const _ = require('lodash');
// let endpoint = "//api.tbk.dingdanxia.com/vip/id_privilege";
let endpoint = "//api.tbk.dingdanxia.com/vip/url_privilege";
let detailEndpoint = "//api.tbk.dingdanxia.com/vip/item_info";
const TAG = "vip ddx goods detail tag";
let req;
(()=>{
  function onSuccess(res){
    // console.log(TAG, "onSuccess is", res);
    if(!res || !res.data){
      return showSimpleToast('没有获取到唯品会链接')
    }
    let we_app_info = {app_id:"wxe9714e742209d35f", page_path:res.data.vipWxUrl}
    _goods.we_app_info = res.data;
    goodsModel.dispatchEvent(new Event2(GoodsModel.events.onPddWeAppInfoLoaded, _goods));
    // goodsModel.setVipDdxSearchResult(res);
  }
  function onFail(res){
    console.log(TAG, "onFail is", res);
  }
  function onComplete(res){
    // console.log(TAG, "onComplete is", res);
  }
  
  req = new DdxRequest(endpoint, null, onSuccess, onFail, onComplete);
  
})();
let detailReq;
(()=>{
  function onSuccess(res){
    if(!res || !res.data){
      return showSimpleToast('没有获取到唯品会详情')
    }
    _.assign(_goods, res.data);
    goodsModel.parseVipGoods(_goods);
    // let we_app_info = {app_id:"wxe9714e742209d35f", page_path:res.data.vipWxUrl}
    // _goods.we_app_info = we_app_info;
    goodsModel.dispatchEvent(new Event2(GoodsModel.events.onPddWeAppInfoLoaded, _goods));
    // goodsModel.setVipDdxSearchResult(res);
  }
  function onFail(res){
    // MainModel.logs.push("vip onfail is " + JSON.stringify(res));
    console.log(TAG, "onFail is", res);
  }
  function onComplete(res){
    // console.log(TAG, "onComplete is", res);
  }
  
  detailReq = new DdxRequest(detailEndpoint, null, onSuccess, onFail, onComplete);
  
})();
let _goods;
export function vipDdxGetGoodsDetail(goods, needDetail){
  _goods = goods;
  if(needDetail){
    detailReq.send(detailEndpoint, {id:goods.goodsId})
  }
  let statParam = {uid:userModel.user.id || 0}
  if(goods.shareParentId){
    statParam.pid = goods.shareParentId;
  }
  // let params = {statParam:userModel.user.mobile || "1000", "chanTag":getECommerceChannelVip(), id:goods.goodsId, url:goods.destUrl}
  let params = {statParam, "chanTag":getECommerceChannelVip(), id:goods.goodsId, url:goods.destUrl}
  // MainModel.logs.push("vip loading is " + needDetail + " data is " + goods.goodsId )
  req.send(endpoint, params);
}

