import {showSimpleToast} from "../../uiuitils/toast";

const {Member} = require("../../remotes/Member");
const {PddMember} = require("../../remotes/PddMember");
const {TMs} = require("../../remotes/TMs");
const {
  endpoints
} = require("../../mc/pddRequests/PddEndpoints");
const {
  goodsModel
} = require("../../mc/GoodsModel");
const { RecommendPageInfo, saveKeys, PageInfo } = require("../recommendPageInfo");

const recommendRequestTypes = {
  forRecommend: "forRecommend",
  forSearch: "forSeach",
}
let requestType;
const channelTypes = [1, 5, 6];
const recommendPageInfo = new RecommendPageInfo();
recommendPageInfo.init(saveKeys.pdd, [5, 6, 1], -1, 10);
let pageInfo = new PageInfo();
export const ii = {
  recommendRequestTypes
};
(() => {
  ii.refreshGoodsList = () => {
    goodsModel.clearPddGoodsRecommend();
    ii.getPddRecommendSign();
  }
})();
(() => {

  function onSuccess(res) {
    if (!res || !res.params) {
      return;
    }
    console.log('rrrrrrrrrrrrr', res);
    let obj;
    try{
      obj = JSON.parse(res.res_string);
    }catch (e){
      return showSimpleToast("pdd recommend get err", e.toString())
    }
    onPddSuccess(obj);
    // fetchPddGoodsRecommendGet(res.params);
  }

  function onError(member) {

  }

  function onNetError(member) {


  }

  function onComplete(member) {

  }


  let request; // = new Member(TMs.TPddGoods, TMs.MPddGoodsRecommendGet, {}, onSuccess, onError, onComplete);
  //到宝贝服务获取商品id列表
  function getPddRecommendSign(type, params) {
    // return;
    pageInfo = recommendPageInfo.getNextPageInfo();
    requestType = type;
    params = params || {};
    switch (type) {
      case recommendRequestTypes.forRecommend:
        params.offset = pageInfo.index * pageInfo.limit;
        // params.offset = goodsModel.offset;
        break;
    }
    params.limit = pageInfo.limit;
    params.channel_type = pageInfo.channel;
    // params.limit = goodsModel.limit;
    // params.channel_type = 4;
    // // 活动商品标记数组，例：[4,7]，4-秒杀，7-百亿补贴，10851-千万补贴，
    // // 10913-招商礼金商品，31-品牌黑标，10564-精选爆品-官方直推爆款，
    // // 10584-精选爆品-团长推荐，24-品牌高佣，其他的值请忽略
    params.activity_tags = [10564, 10584, 24, 4, 7, 10851, 31];
    request = new Member(TMs.TPddGoods, TMs.MPddGoodsRecommendGet, {from:"web",
      "params": params || {}
    }, onSuccess, onError, onComplete);
    request.send();
  }
  ii.getPddRecommendSign = getPddRecommendSign;
})();

let fetchPddGoodsRecommendGet;
let prevSearchId;
let onPddSuccess;
(() => {
  onPddSuccess = onSuccess;
  function onSuccess(res) {
    if (!res || !res.goods_basic_detail_response || !res.goods_basic_detail_response.list) {
      return;
    }
    let list = res.goods_basic_detail_response.list;
    if (!list || list.length == 0) {
      goodsModel.recommendsTotallyFetched = true;
      return;
    }
    prevSearchId = res.goods_basic_detail_response.search_id;
    let signList = [];
    res.goods_basic_detail_response.list.forEach((item) => signList.push(item.goods_sign));
    // MainModel.setPddGoodsList(res.goods_basic_detail_response.list);
    goodsModel.offset += res.goods_basic_detail_response.list.length;
    if (requestType == recommendRequestTypes.forRecommend) {
      goodsModel.isFetchingGoods = false;
      pageInfo.total = res.goods_basic_detail_response.total;
    }
    goodsModel.setPddGoodsList(res.goods_basic_detail_response.list);
  }

  function onError(member) {

  }

  function onNetError(member) {


  }

  function onComplete(member) {

  }
  let request;

  function pdd(params) {
    request = new PddMember(endpoints.goodsRecommendGet(), params, onSuccess, onError, onComplete);
    request.send();
  }
  fetchPddGoodsRecommendGet = pdd
})();
