const { goodsModel } = require("../../mc/GoodsModel");
const MainModel = require("../../mc/MainModel");
const {DdxRequest} = require("../../remotes/DdxMember");
const _ = require("lodash")
let endpoint = "//api.tbk.dingdanxia.com/vip/query";
let endpointUrl = "//api.tbk.dingdanxia.com/vip/url_privilege";
const TAG = "vip ddx search query tag";
let isUrlSearch = false;
function onSuccess(res){
  // console.log(TAG, "onSuccess is", res);
  if(!res || !res.data){
    return;
  }
  if(isUrlSearch){
    let data = res.data;
    let info = data.itemInfo;
    if("couponInfo" in info){
      let couponInfo = info.couponInfo;
      delete info.couponInfo;
      data.couponInfo = couponInfo;
    }
    _.extend(data, info);
    goodsModel.setVipDdxSearchResult({data:[data]});
    // goodsModel.setVipDdxSearchResult({data:[res.data.itemInfo]});
  }else{
    goodsModel.setVipDdxSearchResult(res);
  }
}
function onFail(res){
  console.log(TAG, "onFail is", res);
}
function onComplete(res){
  // console.log(TAG, "onComplete is", res);
}

const req = new DdxRequest(endpoint, null, onSuccess, onFail, onComplete, " 唯品会搜索");

export function vipDdxSearchQuery(params){
  if(req.isConnecting)return;
  isUrlSearch = params.keyword.indexOf("vip.com/") > -1;
  let end = endpoint
  if(isUrlSearch){
    end = endpointUrl;
    params = {url:params.keyword, chanTag:"web", statParam:MainModel.user ? MainModel.user.mobile :"1000", itemInfo:true}
  }
  req.send(end, params);
}

