
import {CrossEvent, CrossMember} from "./CrossMember";
import {f7} from "framework7-react";

let apikey = "2Zgrz6fPI9W2mVCfJuNDyQdaT5fOrW3J";

function formdata (obj = {}) {
  let result = ''
  for (let name of Object.keys(obj)) {
    let value = obj[name];
    result += 
    '\r\n--XXX' +
    '\r\nContent-Disposition: form-data; name="'+name+'”'+ 
    '\r\n' +
    '\r\n' + value
  }
  return result+'\r\n--XXX--'
}

export class DdxRequest{
  constructor(endpoint, params, onSuccess, onFail, onComplete, alias){
    this.endpoint = endpoint;
    this.params = params;
    this.onSuccess = onSuccess;
    this.onFail = onFail;
    this.onComplete = onComplete; 
    this.isConnecting = false;
    this.member = null;
    this.alias = alias;
  }
  send = (endpoint, params, alias)=>{
    this.isConnecting = true;
    endpoint = endpoint || this.endpoint;
    params = params || this.params;
    params.apikey = apikey;
    this.alias = alias || this.alias;
    let member = this.member = new CrossMember();
    member.addEventListener(CrossEvent.EventTypes.onError, this._onError);
    member.addEventListener(CrossEvent.EventTypes.onSuccess, this._onSuccess);
    member.addEventListener(CrossEvent.EventTypes.onComplete, this._onComplete);
    member.load(CrossMember.Methods.get, endpoint, params, {'content-type':'text/plain'});
  }
  _onError = (e)=>{
    console.log(`ddx member on fail is ${this.alias || ''}`, e);
    if(this.onFail){
      this.onFail(e);
    }
  }
  _onSuccess = (e)=>{
    let res = e.res;
    if(!res || !res.data || res.code != 200){
      if(this.onFail){
        this.onFail(e);
      }
      return f7.toast.create({closeTimeout: 3000,text:`订单侠没有返回数据 ${this.alias || '' } `, destroyOnClose:true}).open();
    }
    if(this.onSuccess){
      this.onSuccess(res);
    }
  }
  _onComplete = (e)=>{
    this.isConnecting = false;
    if(this.onComplete){
      this.onComplete();
    }
  }
}

