class Footprint extends GoodsHistoryBase {
  constructor() {
    super(footPrintName, GoodsModel.events.footprintUpdated);
  }
}

class Favor extends GoodsHistoryBase{
  constructor(){
    super(favorName, GoodsModel.events.favorUpdated);
  }
  toggle = (goods)=>{
    let isOn = false;
    let [removed, h] = this.removeGoodsIfExists(goods);
    if(!removed){
      isOn = true;
      this.addGoods2(h);
    } 
    return isOn;
  }
}

const goodsModel = new GoodsModel();
