const _ = require('lodash');
const {JdMember} = require('./JdMember');
const {Member} = require('./Member');
const {PddMember} = require('./PddMember');
const {TbMember} = require('./TbMember');

const fetchStateIdle = "idel";
const fetchStateConnecting = "connecting";
const fetchStateResultOk = "ok";
const fetchStateResultError = "error";
export const states = {
  fetchStateIdle,
  fetchStateConnecting,
  fetchStateResultOk,
  fetchStateResultError
}
export class Request{
  constructor(onResult, onError, onComplete, params, send){
    this.state = fetchStateIdle;
    this.result = null;
    this.errResult = null;
    this.params = params;
    this.onResult = onResult;
    this.onError = onError;
    this.onComplete = onComplete;
    this.send = send;
  }
  getState = ()=>{
    return this.state;
  }
  getData = ()=>{
    let data = this.params;
    if(_.isFunction(data)){
      data = data();
    }
    return data;
  }
  run = ()=>{
    this.send(this.getData());
  }
  send = (params)=>{

  }
}
export class TMRequest extends Request{
  constructor(gateway, team, member, onResult, onError, onComplete, params){
    super(onResult, onError, onComplete, params)
    this.team = team;
    this.member = member;
    if(!team){
      throw 'TMRequest no team here'
    }
    this.gateway = gateway;
  }
  send = (params)=>{
    let data = this.getData();
    data = data || {}
    data.from = "web";
    let member = new Member(this.team, this.member, data, this.onResult, this.onResultError, this.onComplete);
    // member.send(MainModel.pddGateway);
    member.send(this.gateway);
  }
}

export class EndPointRequest  extends Request{
  constructor(endpoint, onResult, onError, onComplete, params){
    super(onResult, onError, onComplete, params)
    this.endpoint = endpoint;
  }
  send = (params)=>{
    let member = new PddMember(this.endpoint, this.getData(), this.onResult, this.onResultError, this.onComplete);
    member.send();
  }
}

export class JdRequest extends Request{
  constructor(onResult, onError, onComplete, params){
    super(onResult, onError, onComplete, params)
  }
  send = (rawQuery)=>{
    let member = new JdMember(this.onResult, this.onResultError, this.onComplete);
    member.send(rawQuery);
  }
}

export class TbRequest extends Request{
  constructor(onResult, onError, onComplete, params){
    super(onResult, onError, onComplete, params)
  }
  send = (params)=>{
    let member = new TbMember(this.onResult, this.onResultError, this.onComplete);
    member.send(params);
  }
}
