import React from 'react';
import '../../../../App.css';
import './../../css.css'
import {App, f7ready, Views} from "framework7-react";
import {f7params} from "./f7params";
import {MainView} from "./views/mainview/MainView";
import {routes} from "./routes";

window.__MUI_USE_NEXT_TYPOGRAPHY_VARIANTS__ = true;
window.React = React;

f7params.routes = routes
class Main extends React.Component {
    _addEvents() {
    }

    render = () => {
        f7ready((f7) => {
            // console.log("f7ready", f7.view.main.router.history.length, f7.view.main.router.history.concat());
        })

        return (
            <App {...f7params}>
                <Views className={"safe-areas"}>
                    <MainView />
                    {/*<View view-init main {...mainViewParams}>*/}
                    {/*    <MainPage/>*/}
                    {/*</View>*/}
                </Views>

            </App>
        )
    }
}

export default Main;
