import {showSimpleToast} from "../../uiuitils/toast";

const {Member} = require("../../remotes/Member");
const {TMs} = require("../../remotes/TMs");
const {
  goodsModel
} = require("../../mc/GoodsModel");
const {TbMember} = require("../../remotes/TbMember");

const _ = require('lodash');


export function fetchTbGoodsDetail(goods, onSuccess, onFail, onComplete) {
  let member;
  let tbMember;
  let _onTbSuccess;
  (() => {
    function onTmSuccess(res) {
      console.log('xxxxxxxxxxx', res);
      if (!res || !res.res_text) {
        return showSimpleToast('tb no res')
      }
      // tbMember.send(res.params);
      let obj;
      try{
        obj = JSON.parse(res.res_text);
      }catch (e){
        return showSimpleToast("parse tb detail error")
      }
      _onTbSuccess(obj);
    }

    function onTmFail(err) {
      console.log('taobao optimus material tm fail', err);
      if (!onFail) {
        return;
      }
      onFail(err, 1);
    }

    function onTmComplete(member) {

    }
    member = new Member(TMs.TTbGoods, TMs.MTbGoodsDetail, {}, onTmSuccess, onTmFail, onTmComplete);
  })();

  (() => {
    let isOk = false;
    _onTbSuccess = onTbSuccess
    function onTbSuccess(res) {
      // console.log('resssssss', res);
      if (!res) {
        return showSimpleToast('淘宝没有返回')
      }
      if (!res.tbk_item_info_get_response) {
        return showSimpleToast('淘宝没有返回数据')
      }
      if (!res.tbk_item_info_get_response.results) {
        return showSimpleToast('淘宝没有返回列表')
      }
      if (!res.tbk_item_info_get_response.results.n_tbk_item) {
        return showSimpleToast('淘宝没有返回商品1')
      }
      if (!res.tbk_item_info_get_response.results.n_tbk_item[0]) {
        return showSimpleToast('淘宝没有返回商品2')
      }
      let data = res.tbk_item_info_get_response.results.n_tbk_item[0];
      _.assign(_goods, data);
      goodsModel.parseTbGoods(_goods);
      isOk = true;
      _onSuccess(_goods);
      isOk = true;
    }

    function onTbFail(err) {
      console.log('taobao optimus material tb fail', err);
      if (!onFail) return;
      onFail(err, 2);
    }

    function onTbComplete(tmMember) {
      if (!onComplete) return;
      onComplete(tmMember, isOk);
    }
    tbMember = new TbMember(onTbSuccess, onTbFail, onTbComplete);
  })();

  let _goods;
  let _onSuccess = onSuccess;
  _goods = goods;
  let params = {
    num_iids: goods.item_id,
    platform: 2
  };
  member.body = {
    params,
    from:"web"
  }
  member.send();
}
