import Member from "../../remotes/Member";
import tms from "../../remotes/TMs";
import {trackingModel} from "../../mc/TrackingModel";
function trackingGetVistorOpenKey(){
    let member = new Member(tms.TTracking, tms.MTrackingGetVisitOpenKey, {}, (res)=>{
        // console.log(`tms.TTracking, tms.MTrackingGetVisitOpenKey res is`, res)
        trackingModel.trackingUuid = res.uuid;
    }, (member)=>{
        console.log(`tms.TTracking, tms.MTrackingGetVisitOpenKey net error is`, member.netError, member.netErrorInfo);
    }, (member)=>{
        console.log(`tms.TTracking, tms.MTrackingGetVisitOpenKey data error is`, member.dataError, member.dataErrorInfo);
    }, (member) =>{
        // console.log(`tms.TTracking, tms.MTrackingGetVisitOpenKey complete is`, member.isNetOk, member.isDataOk, member.isTotallyOk);
    }, null, true, true);
    member.send();
}

export {trackingGetVistorOpenKey}