import {showSimpleToast} from "../../uiuitils/toast";
import {userModel} from "../../mc/UserModel";

const {Member} = require("../../remotes/Member");
const {
  TMs
} = require("../../remotes/TMs");
const {
  goodsModel
} = require("../../mc/GoodsModel");
const {TbMember} = require("../../remotes/TbMember");

const _ = require('lodash');


export function fetchTbGoodsCreateTaokouling( goods, onSuccess, onFail, onComplete) {
  let member;
  let tbMember;
  let _onTbSuccess;
  (() => {
    function onTmSuccess(res) {
      // console.log('xxxxxxxxxxx', res);
      if (!res || !res.res_text) {
        return showSimpleToast("本平台淘宝没有返回数据");
      }
      // tbMember.send(res.params);
      let obj;
      try{
        obj = JSON.parse(res.res_text)
      }catch (e){
        return showSimpleToast("解析淘口令是出现错误")
      }
      if(res.res_ddx_text){
        try {
          goods.ddxTkl = JSON.parse(res.res_ddx_text);
          _onSuccess(goods);
        }catch (e){

        }
      }
      _onTbSuccess(obj);
    }

    function onTmFail(err) {
      console.log('taobao optimus material tm fail', err);
      if (!onFail) {
        return;
      }
      onFail(err, 1);
    }

    function onTmComplete(member) {

    }
    member = new Member(TMs.TTbGoods, TMs.MTbGoodsCreateTaokouling, {}, onTmSuccess, onTmFail, onTmComplete);
  })();

  (() => {
    let isOk = false;
    _onTbSuccess = onTbSuccess;
    function onTbSuccess(res) {
      console.log('eeeeee', res);
      if (!res) {
        return showSimpleToast('淘宝没有返回')
      }
      if (!res.tbk_tpwd_create_response) {
        return showSimpleToast('淘宝没有返回数据')
      }
      if (!res.tbk_tpwd_create_response.data) {
        return showSimpleToast('淘宝没有返回列表')
      }
      goods.taokouling = res.tbk_tpwd_create_response.data;
      isOk = true;
      _onSuccess(_goods);
      isOk = true;
    }

    function onTbFail(err) {
      console.log('taobao optimus material tb fail', err);
      if (!onFail) return;
      onFail(err, 2);
    }

    function onTbComplete(tmMember) {
      if (!onComplete) return;
      onComplete(tmMember, isOk);
    }
    tbMember = new TbMember(onTbSuccess, onTbFail, onTbComplete);
  })();

  let _goods;
  let _onSuccess = onSuccess;
  _goods = goods;
  let params = {
    // item_id: goods.item_id,
    url:(goods.click_url.indexOf('https:') === -1 ? 'https:': '') + goods.click_url,
    // url:'https:' + goods.click_url,
    ext:"yiji",
    user_id:userModel.user.mobile ? userModel.user.mobile : "",
    text:"shuoming"
  };
  member.body = {
    from:"web",
    params
  }
  member.send();
}
