import React from "react";
import RegMobile from "./ui/RegMobile";
import {Page, PageContent} from "framework7-react";


export class LoginPageContent extends React.Component{
    constructor(props) {
        super(props);
        this.state = {};
        this._addEvents();
    }
    _addEvents = ()=>{

    }
    render = () =>{
        return <PageContent  style={{paddingTop:"0px"}}>
            <div>
                <RegMobile />
            </div>
        </PageContent>
    }
}

export function LoginPage(){
    return <Page>
        <LoginPageContent />
    </Page>
}