import {CrossEvent, CrossMember} from "./CrossMember";
import {f7} from "framework7-react";

const gateway = "https://gw-api.pinduoduo.com/api/router";



const TMs = require('./TMs');
const maxRetryCount = 5;
export class PddMember{
  trackingUuid = ""
  constructor(endpoint, body, onSuccess, onError, onComplete){
    this.endpoint = endpoint;
    this.body = body || {};
    this.onSuccess = onSuccess;
    this.onError = onError;
    this.onComplete = onComplete;
    this.retryCount = 0;
  }
  send(){
    let gateway = PddMember.gateway;
    let header = {
      'content-type': 'application/json',
    };

    let member = this.member = new CrossMember();
    member.addEventListener(CrossEvent.EventTypes.onError, this._onError);
    member.addEventListener(CrossEvent.EventTypes.onSuccess, this._onSuccess);
    member.addEventListener(CrossEvent.EventTypes.onComplete, this._onComplete);
    member.load(CrossMember.Methods.post, gateway, this.body, header, (xhr)=>{
      xhr.withCredentials = true;
    });
  }

  _onError = (e)=>{
    if(this.onFail){
      this.onFail(e);
    }
  }
  _onSuccess = (e)=>{
    let res = e.res;
    let result = res;
    if(!result || result.error_response){
      if(this.onFail){
        this.onFail(res);
      }
      return f7.toast.create({closeTimeout: 3000,text:'拼多多没有返回数据', destroyOnClose:true}).open();
    }
    if(this.onSuccess){
      this.onSuccess(res.data);
    }
  }
  _onComplete = (e)=>{
    this.isConnecting = false;
    if(this.onComplete){
      this.onComplete();
    }
  }
}
PddMember.gateway = "https://gw-api.pinduoduo.com/api/router";
export default PddMember;
