import {trackingGetVistorOpenKey} from "../requests/tracking/tracking_get_visitor_open_key";

const {trackingModel, TrackingModel} = require("./TrackingModel");

class TrackingController{
    init = ()=>{
        this._addEvents();
    }
    start = ()=>{
        trackingGetVistorOpenKey();
    }
    _addEvents = ()=>{
        trackingModel.addEventListener(TrackingModel.events.eventTrackingUuidChanged, this._onTrackingUuidChanged);
    }
    _onTrackingUuidChanged = (event)=>{
        // console.log("TrackingController _onTrackingUuidChanged")
    }

}
const trackingController = new TrackingController();
export {trackingController, TrackingController};