import {f7} from "framework7-react";

const { goodsModel } = require("../../mc/GoodsModel");
const {Member} = require("../../remotes/Member");
const {TbMember} = require("../../remotes/TbMember");
const {TMs} = require("../../remotes/TMs");
const { RecommendPageInfo, saveKeys, PageInfo } = require("../recommendPageInfo");

const recommendPageInfo = new RecommendPageInfo();
// recommendPageInfo.init(saveKeys.tb, [6708], 0, 10);
recommendPageInfo.init(saveKeys.tb, [31362, 4093, 27160, 32366, 34616,  4094, 13366, 3786,27446, 30443, 28026, 3756, 4092], 0, 10);
let pageInfo = new PageInfo();

let member;
let tbMember;
let onSuccess;
let onFail;
let onComplete;
(()=>{
  function onTmSuccess(res){
    // console.log('xxxxxxxxxxx', res);
    if(!res || !res.res_text){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索服务器没有返回 `, destroyOnClose:true}).open();
    }
    var data
    try{
      data = JSON.parse(res.res_text);
    }catch (e){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索解析数据错误 `, destroyOnClose:true}).open();
    }
    if(!data.tbk_dg_material_optional_response){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索服务器没有返回 tbk_dg_material_optional_response`, destroyOnClose:true}).open();
    }
    if(!data.tbk_dg_material_optional_response.result_list){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索服务器没有返回 result_list`, destroyOnClose:true}).open();
    }
    if(!data.tbk_dg_material_optional_response.result_list.map_data){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索服务器没有返回 map_data`, destroyOnClose:true}).open();
    }

    // console.log('res res_text is', data.tbk_dg_material_optional_response.result_list.map_data);
    goodsModel.setTbGoodsSearchResult(data.tbk_dg_material_optional_response.result_list.map_data)
    // tbMember.send(res.params);
  }
  function onTmFail(err){
    console.log('taobao optimus material tm fail', err);
    if(!onFail){
      return;
    }
    onFail(err, 1);
  }
  function onTmComplete(member){

  }
  member = new Member(TMs.TTbGoods, TMs.MTbGoodsTbkDgMaterialOptional, {}, onTmSuccess, onTmFail, onTmComplete);
})();

(()=>{
  let isOk = false;
  function onTbSuccess(res){
    // console.log('resssssss', res);
    if(!res){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索没有返回 `, destroyOnClose:true}).open();
    }
    if(!res.tbk_dg_optimus_material_response){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索没有返回数据 `, destroyOnClose:true}).open();
    }
    if(!res.tbk_dg_optimus_material_response.result_list){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索没有返回列表 `, destroyOnClose:true}).open();
    }
    if(!res.tbk_dg_optimus_material_response.result_list.map_data){
      return f7.toast.create({closeTimeout: 3000,text:`淘宝搜索没有返回map `, destroyOnClose:true}).open();
    }
    goodsModel.setTbGoodsList(res.tbk_dg_optimus_material_response.result_list.map_data);
    isOk = true;
  }
  function onTbFail(err){
    console.log('taobao optimus material tb fail', err);
    if(!onFail)return;
    onFail(err, 2);
  }
  function onTbComplete(tmMember){
    if(!onComplete)return;
    onComplete(tmMember, isOk);
  }
  tbMember = new TbMember(onTbSuccess, onTbFail, onTbComplete);
})();

export function tbGoodsSearch(params){
  member.body = {params, from:"web"}
  member.send();
}
