import React from 'react';
import ReactDOM from "react-dom";
import Main from "./Main";

import "clipboard";
// import  "../../f7common/f7common.css";
import 'framework7-icons';
import Framework7 from 'framework7/lite-bundle';
import Framework7React from 'framework7-react';
import 'framework7/framework7-bundle.min.css'
import 'framework7/components/panel/panel.css'
import "./../../css/index.css";
import {mainController} from "./../../mc/MainController";
import {isMobile} from "./../../utils/Navigate";
import {title} from "./../../confs/env"
Framework7.use(Framework7React);

document.title = title;
mainController.init();
mainController.start();

const renderApp = () => ReactDOM.render(
    <React.StrictMode>
        <Main />
    </React.StrictMode>,
    document.getElementById('root')
);
if(isMobile() === false){
    document.getElementById('root').style.width =  "750px";
}
function run() {
    renderApp();
    if (module.hot) {
        module.hot.accept(renderApp);
    }
}

run();