
import {userController} from "./UserController";
import {trackingController} from "./TrackingController";
import {trackingModel, TrackingModel} from "./TrackingModel";
import {mainModel, MainModel} from "./MainModel";
import Member from "../remotes/Member";
import {websiteConf} from "../confs/local.conf";
import _ from 'lodash';
import {listenPackWaitingList} from "../requests/pack/packWaitingList";
import {f7} from "framework7-react";
import {pageController} from "./PageController";
import {autoDebugController} from "./AutoDebugController";
import {goodsController} from "./GoodsController";

class MainController{
    init = ()=>{
        // console.log('xxxxxxx',window.location.href)
        Member.gateway = websiteConf.gateway;
        Member.dispatcher = mainModel;
        this._addEvents();
        mainModel.init();
        userController.init();
        pageController.init();
        trackingController.init();
        goodsController.init();
        autoDebugController.init();
        this.start();
    }
    start = ()=>{
        if(this._isLaunched)return;
        this._isLaunched = true;
        trackingController.start();
        pageController.start();
        autoDebugController.start();

    }
    _addEvents = ()=>{
        trackingModel.addEventListener(TrackingModel.events.eventTrackingUuidChanged, this._onTrackingUuidChanged);
        mainModel.addEventListener(MainModel.events.addWaitingMember, this._onAddWaitingMember);
        mainModel.addEventListener(MainModel.events.routerBack, this._onRouterBack);
        listenPackWaitingList();
    }
    _onAddWaitingMember = (event)=>{
        if(_.isFunction(event.data) === false){
            console.error('_onAddWaitingMember is not a function', event);
            return;
        }
        mainModel.waitingMembers.push(event.data);
        this._runWaitingMembers();
    }
    _onTrackingUuidChanged = (event) =>{
        // console.log("main ctrollder on tracking uuid changed")
        this._startAll();
        this._runWaitingMembers();
    }
    _runWaitingMembers(){
        if(!trackingModel.trackingUuid)return;
        if(mainModel.waitingMembers.length <= 0)return;
        let runMemberFunc = mainModel.waitingMembers.shift();
        runMemberFunc();
        setTimeout(()=>{
            this._runWaitingMembers();
        }, 50);
    }
    _startAll = ()=>{
        userController.start();
    }
    _onRouterBack = (event)=>{
        let data = event.data;
        console.log('ooo',data)
        // f7.view.main.router.back(data.url, data.options);
        f7.view.main.router.back();
        // f7.views
    }
    _initHistory = ()=>{
        this._initHistory();
        (function(history){
            var pushState = history.pushState;
            history.pushState = function(state) {
                if (typeof history.onpushstate == "function") {
                    history.onpushstate({state: state});
                }
                // ... whatever else you want to do
                // maybe call onhashchange e.handler
                // console.log('xxxxxxxxxxxxxx', state);
                return pushState.apply(history, arguments);
            };
        })(window.history);
    }
}

const mainController = new MainController();
export {mainController, MainController}